<template>
  <div class="video-wrapper">
    <ali-player ref="aliplayer"
      v-if="updatePlayer"
      :source="sourceData"
      :progressShow="progress"
      :components="components"
      :width="videoWidth"
      :height="videoHeight"
      :startTime="startTime"
      @pause="pauseChange"
      @ended="endedChange"
      @timeupdate="timeupdateChange"
      @startSeek="startSeekChange"
      @completeSeek="completeSeekChange"
    ></ali-player>
    <div style="float: left; margin-top: 10px">
      <button @click="updateProgress"> {{progress ? '隐藏' : '显示'}}进度条 </button>
      <span>当前播放时间：<strong v-text="time"></strong></span>
    </div>
  </div>
</template>

<script>
import AliPlayer from './aliplayer.vue'
export default {
  components: {AliPlayer},
  data () {
    // let self = this
    return {
      videoWidth: '500px',
      videoHeight: '400px',
      updatePlayer: true,
      progress: true,
      time: '',
      startTime: 30,
      seekStartTime: 0,
      seekEndTime: 0,
      isSeeking: false,
      sourceData: JSON.stringify({
        'FD': 'https://look.chinahrt.com.cn/courseyun/rx/transcode/20206/317690bd-f600-43a0-87a7-6c6182da4607/283001-m3u8.m3u8',
        'LD': 'https://look.chinahrt.com.cn/u/u/courseyun/admin/transcode/202112/317690bd-f600-43a0-87a7-6c6182da4607/283002-m3u8.m3u8',
        'SD': 'https://look.chinahrt.com.cn/courseyun/rx/transcode/20206/317690bd-f600-43a0-87a7-6c6182da4607/283003-m3u8.m3u8'
      }),
      sourceData2: JSON.stringify({
        'FD': 'https://look.chinahrt.com.cn/u/courseyun/7d1e9d587bc648cbbdb869c1300967f8/transcode/20216/5cf04cc0-2d74-4ae2-ab10-3ee59d4388a5/283006-mp4.mp4',
        'LD': 'https://look.chinahrt.com.cn/u/courseyun/7d1e9d587bc648cbbdb869c1300967f8/transcode/20216/5cf04cc0-2d74-4ae2-ab10-3ee59d4388a5/283001-mp4.mp4',
        'SD': 'https://look.chinahrt.com.cn/u/courseyun/videos/7d1e9d587bc648cbbdb869c1300967f8/MCJY/20216/db4c14189abe4b31bb553a59150c0eeb.mp4'
      }),
      skins: [
        {
          'name': 'bigPlayButton',
          'align': 'blabs',
          'x': 30,
          'y': 80
        },
        {
          'name': 'H5Loading',
          'align': 'cc'
        },
        {
          'name': 'errorDisplay',
          'align': 'tlabs',
          'x': 0,
          'y': 0
        },
        {
          'name': 'infoDisplay'
        },
        {
          'name': 'tooltip',
          'align': 'blabs',
          'x': 0,
          'y': 56
        },
        {
          'name': 'thumbnail'
        },
        {
          'name': 'controlBar',
          'align': 'blabs',
          'x': 0,
          'y': 0,
          'children': [
            {
              'name': 'progress',
              'align': 'blabs',
              'x': 0,
              'y': 44
            },
            {
              'name': 'playButton',
              'align': 'tl',
              'x': 15,
              'y': 12
            },
            {
              'name': 'timeDisplay',
              'align': 'tl',
              'x': 10,
              'y': 7
            },
            {
              'name': 'fullScreenButton',
              'align': 'tr',
              'x': 10,
              'y': 12
            },
            {
              'name': 'setting',
              'align': 'tr',
              'x': 15,
              'y': 12
            },
            {
              'name': 'volume',
              'align': 'tr',
              'x': 5,
              'y': 10
            }
          ]
        }
      ],
      components: [
        // {
        //   name: 'StartADComponent',
        //   type: window.AliPlayerComponent.StartADComponent,
        //   args: ['https://img.alicdn.com/tfs/TB1byi8afDH8KJjy1XcXXcpdXXa-1920-514.jpg', 'https://promotion.aliyun.com/ntms/act/videoai.html', 3]
        // }, // 视频开始时广告组件
        {
          name: 'QualityComponent',
          type: window.AliPlayerComponent.QualityComponent,
          args: function (a, b) {
            console.log('切换', a, b)
            // self.reloadPlayer()
          }
        }, // 清晰度组件
        {
          name: 'PauseADComponent',
          type: window.AliPlayerComponent.PauseADComponent,
          args: ['https://img.alicdn.com/tfs/TB1byi8afDH8KJjy1XcXXcpdXXa-1920-514.jpg', 'https://baidu.com']
        } // 视频暂停时广告组件
      ]
    }
  },
  methods: {
    pauseChange (e) {
      console.log('暂停', e, '播放时间点', e.getCurrentTime())
      this.time = e.getCurrentTime()
    },
    endedChange (e) {
      // console.log('结束', e)
    },
    startSeekChange (e) {
      // this.seekStartTime = e.getCurrentTime()
      // console.log('进度条开始拖拽', e.getCurrentTime())
      // this.isSeeking = true
    },
    completeSeekChange (e) {
      // this.seekEndTime = e.getCurrentTime()
      // console.log('进度条拖拽结束', e.getCurrentTime(), e, this.$refs.aliplayer)
      // if (this.seekStartTime + 2 < this.seekEndTime) {
      //   console.log('快进了', this.seekStartTime, this.seekEndTime)
      //   this.$refs.aliplayer.seek(this.seekStartTime)
      //   this.isSeeking = false
      //   // this.$refs.aliplayer.seek(this.seekStartTime)
      //   // e.loadByUrl(e.getSourceUrl(), this.seekStartTime)
      //   // e._startPlay()
      //   // this.$refs.aliplayer.play()
      // }
    },
    timeupdateChange (e) {
      // if (!this.isSeeking) {
      //   this.seekStartTime = e.getCurrentTime()
      // }
    },
    reloadPlayer () {
      this.$refs.aliplayer.reloadPlayer()
    },
    updateProgress () {
      this.updatePlayer = false
      this.$nextTick(() => {
        this.progress = !this.progress
        this.updatePlayer = true
      })
    }
  }
}
</script>

<style scoped>
.prism-player .prism-speed-selector {
  display: none;
}
</style>



